import SalesCard from "../SalesCard";
import LineChart from "./LineChart";
import MemberChart from "./MemberChart";
export default {
  components: { SalesCard, LineChart, MemberChart },
  props: {
    index: {
      type: Number,
    },
  },
  data() {
    return {
      active: 1,
    };
  },
  computed: {},
  methods: {
    handleProgressBg(index) {
      if (this.active === index) {
        return "#fff";
      }
      return "";
    },
    onToggle(index) {
      this.active = index;
    },
    // 收起
    onChangeClick() {
      this.$emit("change");
    },
  },
};
